import React, { Component } from 'react';
//import { Button, Menu, Icon} from 'antd';
import { CSSTransition,TransitionGroup} from 'react-transition-group';
import { createStore } from 'redux';
import LazyLoad from 'react-lazyload';
import { Link } from 'react-router-dom';

import '../css/reset.css';
import '../css/index.scss';
import '../font/iconfont.css';
import Reducer from '../common/reducer';

const store = createStore(Reducer);
const viewPortH = document.documentElement.clientHeight;

class App extends Component {
	handleNavToggle = (e) => {
  		store.dispatch({type:'toggleNav'});
		e.stopPropagation();
  	}
  	render() {
    	return (
			<section>
				<NavBar />
				<MainScreen handleNavToggle = {this.handleNavToggle} />
				<Skills />
				<Works />
				<PersonalInfo />
				<Contacts />
				<Footer />
			</section>
    	)
  	}
}

function scrollToAnchor(e,anchorId) {
	if(anchorId){
		var oEvent = e || window.event;
		let anchorEle = document.getElementById(anchorId);
		if(anchorEle && anchorEle.scrollIntoView){
			anchorEle.scrollIntoView({behavior:'smooth',block:'start'});
			oEvent.preventDefault();
		}
	}
}

class NavBar extends Component { 
	componentWillMount() {
		this.unsubscribe = store.subscribe(() => {
			this.forceUpdate();
		})
	}
    render() {
		return (
			<section id="navBar" className={store.getState().isNavCur?'nav-container cur':'nav-container'}>
				<ul className="nav-list">
					<li>
						<a className="anchor" href="#navBar">扉页
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a className="anchor" href="#mySkills" onClick={(e)=>{scrollToAnchor.call(this,e,'mySkills')}}>技能
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a className="anchor" href="#myWorks" onClick={(e)=>{scrollToAnchor.call(this,e,'myWorks')}}>作品
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a className="anchor" href="#selfInfo" onClick={(e)=>{scrollToAnchor.call(this,e,'selfInfo')}}>关于我
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a className="anchor" href="#contacts" onClick={(e)=>{scrollToAnchor.call(this,e,'contacts')}}>联系方式
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
				</ul>
			</section>
		)
	}
	componentWillUnmount() {
		this.unsubscribe();
	}
}

