const Reducer = (state={isNavCur:false,curTabIndex:0},action)=> {
	switch(action.type) {
		case 'toggleNav':
			return {...state,isNavCur:(state.isNavCur?false:true)};
		case 'tabWork':
			return {...state,curTabIndex:action.payload};
		default:
			return state;
	}
}
export default Reducer;


